.header {
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
}

html {
  --color-for-link: #00acdc;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

body {
  margin: 0;
}

.nav {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.nav__item {
  align-items: center;
  margin-left: 20px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  text-align: center;
  padding: 23px 0;
  font-size: 12px;
  text-decoration: none;
  display: block;
}

.logo {
  padding: 10px 0;
  display: flex;
}

.is-active {
  color: var(--color-for-link);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--color-for-link);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link:hover {
  color: var(--color-for-link);
  position: relative;
}
